<script>
    import {
        Button,
        Modal,
        ModalBody,
        ModalFooter,
        ModalHeader,
        Dropdown,
        DropdownItem,
        DropdownMenu,
        DropdownToggle,
    } from "sveltestrap";
    import { marked } from "marked";
    import { serverUrl, uid } from "./store";
    import axios from "axios";
    import qs from "qs";

    export const update = (introduce, func) => async () => {
        const { title, text } = introduce;
        console.log(introduce);
        const res = await axios.get(
            $serverUrl +
                `/api/intro/update/${introduce._id}?` +
                qs.stringify({
                    uid: $uid,
                    title,
                    text,
                }),
            { withCredentials: true },
            { uid: $uid }
        );
        console.log(res);
        if (!res.data._id) return alert(" ");
        alert(".");
        tempText = text;
        tempTitle = title;
        func();
    };

    export const create = (introduce, func) => async () => {
        const { title, text } = introduce;
        const res = await axios.get(
            $serverUrl +
                `/api/intro/create?` +
                qs.stringify({
                    uid: $uid,
                    title,
                    text,
                }),
            { withCredentials: true },
            { uid: $uid }
        );
        if (!res.data == "ok") return alert(" ");
        alert(" ");
        func();
    };

    export const del = (introduce, func) => async () => {
        const res = await axios.get(
            $serverUrl + `/api/intro/delete/${introduce._id}`,
            { withCredentials: true }
        );
        if (!res.data == "ok") return alert(" ");
        alert(" ");
        func();
    };

    export const doLike = (introduce, func) => async () => {
        const res = await axios.get(
            $serverUrl +
                `/api/intro/like/${introduce._id}` +
                qs.stringify({
                    uid: $uid,
                }),
            { withCredentials: true }
        );
        if (!res.data == "ok") return alert("   .");
        func();
    };

    export const unLike = (introduce, func) => async () => {
        const res = await axios.get(
            $serverUrl +
                `/api/intro/unlike/${introduce._id}` +
                qs.stringify({
                    uid: $uid,
                }),
            { withCredentials: true },
            { uid: $uid }
        );
        if (!res.data == "ok") return alert("   .");
        func();
    };

    export let showModal = false;
    export let createMode = false;
    export let introduce;
    let { title, text } = introduce;
    let tempText = text;
    let tempTitle = title;
    $: source = marked(text);
    export let modifyMode = false;
    let isOwner = false;
    $: likeN = 0 + isLike;
    let isLike = false;
    const modifyToggle = () => {
        modifyMode = !modifyMode;
        resize();
    };
    const resize = () => {
        document.querySelector(".markdown-editor__right-panel").style.width =
            100 - (modifyMode | createMode) * 50 + "%";
        checkUser();
    };
    const toggle = () => (showModal = !showModal);
    const cancel = async () => {
        text = tempText;
        title = tempTitle;
        modifyToggle();
    };
    const toggleLike = () => {
        isLike = !isLike;
    };
    const checkUser = () => {
        // let uid = parseCookie(document.cookie).uid.substring(3);
        // console.log(uid);
        // uid = uid.substring(0, uid.length - 1);
        // console.log(uid);
        if ($uid === introduce.user._id) isOwner = true;
        console.log("from check user" + uid);
        console.log(isOwner);
    };

    const goTarget = (e) => {
        console.log(e.target.text);
        showModal = !showModal;
    };
</script>

<Modal isOpen={showModal} on:open={resize} fullscreen {toggle}>
    <ModalHeader {toggle}>
        {#if modifyMode | createMode}
            <textarea class="title" bind:value={title} />
        {:else}
            <h1>{title}</h1>
        {/if}
    </ModalHeader>
    <ModalBody>
        <div class="markdown-editor">
            {#if modifyMode || createMode}
                <div class="markdown-editor__left-panel">
                    <textarea
                        bind:value={text}
                        class="markdown-editor__source"
                    />
                </div>
            {/if}
            <div class="markdown-editor__right-panel">
                <div
                    class="markdown-editor__output"
                    oncontextmenu="return false"
                    ondragstart="return false"
                    onselectstart="return false"
                >
                    {@html source}
                </div>
            </div>
        </div>
    </ModalBody>
    <ModalFooter>
        {#if modifyMode}
            <Button
                class="apply button"
                color="primary"
                on:click={update(introduce, modifyToggle)}></Button
            >
            <Button class="cancel button" color="primary" on:click={cancel}
                ></Button
            >
        {:else if createMode}
            <Button
                class="apply button"
                color="primary"
                on:click={create(introduce, toggle)}></Button
            >{:else if isOwner === true}
            <Dropdown direction="up">
                <DropdownToggle color="primary" caret
                    >Like {likeN}</DropdownToggle
                >
                <DropdownMenu>
                    {#each introduce.like_people as person}
                        <DropdownItem on:click={goTarget}
                            >{person.nickname}</DropdownItem
                        >
                    {/each}
                </DropdownMenu>
            </Dropdown>

            <Button
                class="remove button"
                color="secondary"
                on:click={modifyToggle}></Button
            >
            <Button
                class="remove button"
                color="secondary"
                on:click={del(introduce, toggle)}></Button
            >
        {:else}
            <Button
                class="like button"
                color="primary"
                on:click={isLike
                    ? unLike(introduce, toggleLike)
                    : doLike(introduce, toggleLike)}>Like {likeN}</Button
            >
        {/if}
    </ModalFooter>
</Modal>

<style>
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        display: inline-block;
        width: auto;
    }
</style>
